<template>
  <div>
    <div>
      <!-- {{ toDay }} -->
    </div>
    <DatePicker v-model="toDay">
      <template #default="{ inputValue, inputEvents }">
        <input
          class="px-3 py-1 my-4 rounded border_blue"
          :value="inputValue"
          v-on="inputEvents"
        />
      </template>
    </DatePicker>
    <div>
      <button type="button"
        class="btn btn-blue fw-bolder button_padding button_border-right"
        @click="getReportData(1)">年</button>
      <button type="button"
       class="btn btn-blue fw-bolder button_padding"
        @click="getReportData(2)">月</button>
      <button type="button"
        class="btn btn-blue fw-bolder button_padding button_border-left"
        @click="getReportData(3)">日</button>
    </div>
